
.skillsContainer {
  display: grid;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-top: none;
  background-color: white;
  grid-gap: 1px;
  grid-template:
  "nameBlock"230px
  "skills"1fr
  / 1fr
}

.nameBlock {
  grid-area: nameBlock;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.navMenu {
  display: flex;
  padding: 0 0 76px 20px;
}

.descrBlock {
  font-weight: 500;
  font-size: 60px;
  line-height: 72px;
  color: #000000;
}

.skills {
  grid-area: skills;
  border-top: 1px solid rgba(0, 0, 0, 0.3);
}

