
.projectContainer {
  display: grid;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-top: none;
  background-color: white;
  grid-gap: 1px;
  grid-template:
  "nameBlock" 230px
   "projects" 1fr
  / 1fr;
}

.projects{
  border-top: 1px solid rgba(0, 0, 0, 0.3);
  display: flex;
  flex-wrap: wrap;
  grid-area: projects;
}


.nameBlock {
  grid-area: nameBlock;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}